import React from "react";

import { Layout } from "../preComponents";
import { StyledSection, StyledDescription  } from './styled'
import { TextCol, StyledHeadline, StyledImageFour } from "../Misto/components/PlaceIntro/styled";
import { SingleImage } from "../preComponents/Image";

import dary1 from '../../assets/photos/dary1.jpg'
import dary3 from '../../assets/photos/dary3.jpg'
import dary4 from '../../assets/photos/dary4.jpg'
import dary5 from '../../assets/photos/dary5.jpg'

export type GiftsDataType = {
    id: string
    images: SingleImage | SingleImage[]
    description1: string
    description2: string
    description3: string
}

const fourImages: SingleImage[] = [
    { src: dary4, alt: 'Fotka svateba kamardky 2024'},
    { src: dary5, alt: 'Martin'},
    { src: dary3, alt: 'Elika na tboe'},
    { src: dary1, alt: 'Fotka ze panlska 2022'},
]

const giftsData: GiftsDataType = {
    id: "gifts",
    images: fourImages,
    description1: 
    'Jsme upmn astni, e budeme moct tento den oslavit s vmi! Dary s sebou prosm nevozte, mme mal byt a nemli bychom je kam dt. Elika nem rda zbytenosti a Martin se neum nepotebnch vc zbavovat, take by hrozily jen nepjemnosti. Takt jdla a pit bude na mst hodn, tak prosm nic nevozte.',
    description2: 
      'Velice ocenme, kdy nm msto daru pomete s tm, abychom si svatebn den vichni spolen uili.',
    description3:
      'Pokud byste nm peci jen chtli nco vnovat, msto fyzickch dar nm udlte radost finannm pspvkem do spolenho ivota.',

}

const Gifts: React.FC = () => {

  const { id, images, description1, description2, description3 } = giftsData  

  return (
    <Layout.Container >
      <StyledSection id={id} column={2} >  
        <StyledImageFour images={images} />
        <TextCol>
          <StyledHeadline>
            Svatebn dary
          </StyledHeadline>
          <StyledDescription>{description1}</StyledDescription>
          <StyledDescription>{description2}</StyledDescription>
          <StyledDescription>{description3}</StyledDescription>
        </TextCol>
      </StyledSection>
    </Layout.Container>
  )

}

export default Gifts